#sub5 {
    width: 100%;
    .container {
        width: 100%;
        .title {
            max-width: 1100px;
            margin: 0 auto;
            padding: 30px 0 20px 0;
            h2 {
                img {
                    width: auto;
                }
            }
            .small-txt {
                display: flex;
                align-items: center;
                justify-content: right;
                img {
                    padding-right: 5px;
                }
                h4 {
                    font-weight: 400;
                    color: #222;
                    font-size: 12px;
                }
            }
        }
        .content {
            width: 100%;
            ul {
                width: 100%;
                li {
                    width: 100%;
                    .gap {
                        width: 100%;
                        .sub-content1 {
                            background: url(../../../../public/img/starbucks/sub5/msr_bn_bg1.jpg) 50% 100%;
                            height: 400px;
                            width: 100%;
                            position: relative;
                            overflow: hidden;
                            .col-box {
                                position: relative;
                                left: 18%;
                                top: 30%;
                                .txt-box {
                                    > img {
                                        width: 25%;
                                    }
                                }
                                .btn-box {
                                    display: flex;
                                    button {
                                        margin-top: 10px;
                                        display: block;
                                        background-color: transparent;
                                        color: #000;
                                        font-size: 16px;
                                        font-weight: 500;
                                        border: 2px solid #000;
                                        border-radius: 3px;
                                        width: 204px;
                                        height: 40px;
                                        cursor: pointer;
                                        transition: all 0.3s ease-in-out;
                                        margin-right: 5px;
                                        &:hover {
                                            background-color: #000;
                                            color: #fff;
                                            text-decoration: underline;
                                        }
                                    }
                                }
                            }
                        }
                        .sub-content2 {
                            background: url(../../../../public/img/starbucks/sub1/newsArea_bg2.jpg) no-repeat 50% 100%;
                            background-size:contain;
                            height: 70px;
                            width: 100%;
                            position: relative;
                            display: flex;
                            .left-box {
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 60%;
                                height: 70px;
                                background-color: #111;
                            }
                            .right-box {
                                position: absolute;
                                top: 0;
                                right: 0;
                                width: 40%;
                                height: 70px;
                                background: url(../../../../public/img/starbucks/sub1/useInfo_bg.jpg);
                            }
                            .row-box {
                                width: 100%;
                                max-width: 1100px;
                                margin: 0 auto;
                                position: relative;
                                .row1 {
                                    position: absolute;
                                    top: 0;
                                    left: 2%;
                                    height: 70px;
                                    width: 729px;
                                    display: flex;
                                    align-items: center;
                                    img {
                                        &:nth-child(1) {
                                        width: 63px;
                                        height: 17px;
                                        margin-right: 15px;
                                        }
                                        &:last-child {
                                        width: 36px;
                                        height: 36px;
                                        position: relative;
                                        right: 5%;
                                        }
                                    }
                                    .notice-box {
                                        width: 100%;
                                        overflow-y: hidden;
                                        height: 13px;
                                        cursor: pointer;
                                        ul {
                                            margin-top: -24px;
                                            li {
                                                margin-bottom: 11px;
                                                h4 {
                                                    color: #fff;
                                                    font-size: 13px;
                                                    font-weight: 500;
                                                    &:hover {
                                                        text-decoration: underline;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                                .row2 {
                                    position: absolute;
                                    bottom: 0;
                                    right: 3%;
                                    width: 317px;
                                    height: 70px;
                                    img {

                                        &:nth-child(1) {
                                            margin-right: 15px;
                                            position: relative;
                                            top: 16%;
                                            }
                                        &:nth-child(2) {
                                            position: relative;
                                            bottom: 5%;
                                            }
                                        &:last-child {
                                        width: 36px;
                                        height: 36px;
                                        position: relative;
                                        left:  13%;
                                        bottom: 7%;
                                            }
                                        }
                                    }
                                }
                            }
                        .sub-content3 {
                            background: url(../../../../public/img/starbucks/sub5/msr_bn01.jpg) 0% 100%;
                            width: 100%;
                            height: 430px;
                            display: flex;
                            align-items: center;
                            justify-content: right;
                            .col-box {
                                position: relative;
                                right: 5%;
                                top: 0;
                                .txt-box {
                                        img {
                                            width: auto;
                                            display: block;
                                        }
                                    }
                                .btn-box {
                                    display: flex;
                                    flex-flow: row wrap;
                                    width: 600px;
                                    margin-top: 5px;
                                    button {
                                            margin: 13px 5px 0 0;
                                            display: block;
                                            background-color: transparent;
                                            color: #fff;
                                            font-size: 16px;
                                            font-weight: 500;
                                            border: 2px solid #fff;
                                            border-radius: 3px;
                                            width: 204px;
                                            height: 40px;
                                            transition: all 0.3s ease-in-out;
                                            &:hover {
                                                background-color: #Fff;
                                                color: #000;
                                                text-decoration: underline;
                                            }
                                    }
                                }
                            }
                        }
                        .sub-content4 {
                            background: url(../../../../public/img/starbucks/sub5/msr_bn02.jpg) 80% 100%;
                            width: 100%;
                            height: 430px;
                            display: flex;
                            align-items: center;
                            justify-content: left;
                            .col-box {
                                position: relative;
                                left: 19%;
                                .txt-box {
                                        img {
                                            width: auto;
                                            display: block;
                                        }
                                    }
                                .btn-box {
                                    display: flex;
                                    flex-flow: row wrap;
                                    width: 600px;
                                    margin-top: 28px;
                                        button {
                                            margin: 7px 5px 0 0;
                                            display: block;
                                            background-color: transparent;
                                            color: #000;
                                            font-size: 16px;
                                            font-weight: 500;
                                            border: 2px solid #000;
                                            border-radius: 3px;
                                            width: 204px;
                                            height: 40px;
                                            cursor: pointer;
                                            transition: all 0.3s ease-in-out;
                                            &:hover {
                                                background-color: #000;
                                                color: #fff;
                                                text-decoration: underline;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }  
                }
            }
        }
    }